<template>
  <div>
    <v-card>
      <v-card-text>
        <v-form>
          <v-container>
            <v-col cols="12" md="4">
              <v-text-field v-model="name" prepend-icon="address-card" label="カード名" />
            </v-col>
            <v-col cols="12" md="4">
              <v-text-field v-model="price" prepend-icon="usd-circle" label="値段" />
            </v-col>
          </v-container>
          <v-card-actions>
            <v-btn color="orange" @click="search()">検索</v-btn>
          </v-card-actions>
        </v-form>
      </v-card-text>
    </v-card>

    <v-card
    class="mx-auto"
    max-width="344"
  >
    <v-img
      src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
      height="200px"
    ></v-img>

    <v-card-title>
      Top western road trips
    </v-card-title>

    <v-card-subtitle>
      1,000 miles of wonder
    </v-card-subtitle>

    <v-card-actions>
      <v-btn text>Share</v-btn>

      <v-btn
        color="purple"
        text
      >
        Explore
      </v-btn>

      <v-spacer></v-spacer>

      <v-btn
        icon
        @click="show = !show"
      >
        <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
      </v-btn>
    </v-card-actions>

    <v-expand-transition>
      <div v-show="show">
        <v-divider></v-divider>

        <v-card-text>
          I'm a thing. But, like most politicians, he promised more than he could deliver. You won't have time for sleeping, soldier, not with all the bed making you'll be doing. Then we'll go with that data file! Hey, you add a one and two zeros to that or we walk! You're going to do his laundry? I've got to find a way to escape.
        </v-card-text>
      </div>
    </v-expand-transition>
  </v-card>

    <v-card class="mx-auto" max-width="344" v-for="card in cards" v-bind:key="card.img">
      <v-img :src="card.img" height="200px"></v-img>
      <v-card-title>
        {{ card["name"] }}
      </v-card-title>
      <v-card-subtitle> 
        {{ card["price"] }}
      </v-card-subtitle>
      <v-card-actions>
        <v-btn>Share</v-btn>
        <v-btn color="purple" text>Explore</v-btn>

      <v-spacer></v-spacer>
      </v-card-actions>

    <v-expand-transition>
      <div v-show="show">
        <v-divider></v-divider>
      </div>
    </v-expand-transition>
  </v-card>
  </div>
</template>

<style>
  img {
    width: 100px;
  }
</style>
<script>
export default {
  data () {
    return {
      cards: [],
      name: '',
      price: '',
    }
  },
  methods: {
    search () {
      const params = { card_name: this.name, card_price: this.price};
      if (this.price === "") {
        params["card_price"] = 10 ** 20
      } 
      const qs = new URLSearchParams(params);
      fetch(`http://127.0.0.1:5000/api?${qs}`)
      .then( response => {
        return response.json()
      })
      .then( response => {
        this.cards = [];
        for (let i = 0; i < response.length; i++) {
          let card = {
            "name": response[i]['name'],
            "price": response[i]['price'],
            "url": response[i]['url'],
            "img": response[i]['img']
          };
          this.cards.push(card)
        }
      })
      .catch( (err) => {
        this.msg = err // エラー処理
      });
    }
  }
}
</script>